import {MDBCol, MDBContainer, MDBRow} from 'mdbreact';
import React from 'react';
import styles from './Terms.module.scss';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';
import {Document} from 'react-pdf/dist/esm/entry.webpack';
import {Page} from 'react-pdf';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';

interface ITermsProps {

}

class Terms extends React.PureComponent<ITermsProps, any> {
  state = {
    pageNumber: 1,
    numPages: 1,
  }

  renderPages = () => {
    const pages = [];
    for (let i = 1; i <= this.state.numPages; i++) {
      pages.push(<div key={i} style={{marginTop: 10}}><Page pageNumber={i}/></div>);
    }
    return pages;
  }

  render() {
    return (
      <MDBContainer fluid className={styles.termsContainer}>
        <MDBRow>
          <MDBCol>
            <CrispChatContainer/>
            <ScrollToTopExtended bottom={95} scrollOnRender smooth/>
            <Document
              file="/assets/terms.pdf"
              onLoadSuccess={({numPages}) => this.setState({pageNumber: 1, numPages})}
            >
              {this.renderPages()}
            </Document>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );
  }
}

export default Terms;
